/**
 * 
 * @param {element} node 
 * @param {string} type 
 * @param {function} listener 
 */
const on = (node, type, listener) => {
  node.addEventListener(type, listener, false);
};
/**
 * 
 * @param {element} node 
 * @param {string} type 
 * @param {function} listener 
 */
const off = (node, type, listener) => {
  node.removeEventListener(type, listener, false);
};

export { on, off };
